import React, { useEffect, useState } from "react";
import Foreground from "./Foreground";
import MyItems from "./MyItems";
import { useTranslation } from "react-i18next";
import VipButton from "../../components/AppBar/VipButton";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../store";
import { fetchUserInfo } from "../../store/userInfoSlice";
import { Payment } from "@mui/icons-material";
import { fetchRecharge, setIsPaymentSuccess, setShowPaymentStatusModal } from "../../store/rechargeSlice";
import { CaseInfo } from "../../type/rechargeType";
import { getFontClass } from "../../utils/helper";
import Modal from "../../components/Modal";

const Member: React.FC = () => {
  const { t } = useTranslation();
  const dispatch: AppDispatch = useDispatch();
  const accountInfo = useSelector(
    (state: RootState) => state.userInfo.userInfoData
  );
  const payment = useSelector((state: RootState) => state.recharge.rechargeData);
  const language = useSelector((state: RootState) => state.init.language);
  const isPaymentSuccess = useSelector((state: RootState) => state.recharge.isPaymentSuccess);
  const showPaymentStatusModal = useSelector((state: RootState) => state.recharge.showPaymentStatusModal);
  const [isVip, setVip] = useState(false);
  const [monthlyAmount, setMonthlyAmount] = useState<string>("9999999999");

  useEffect(() => {
    if(!payment) {
      dispatch(fetchRecharge({}));
    } else {
      const taMonthlyAmount: (CaseInfo | null) = payment?.payMonthCaseInfos.find((item: any) => item.pc_id === "201") ?? null;
      if (taMonthlyAmount) {
        setMonthlyAmount(taMonthlyAmount[`amount_${language}`] ?? "9999999999");
      }
    }
  }, [payment]);

  useEffect(() => {
    const currentUnixTime = Math.floor(Date.now() / 1000);
    if (
      Number(accountInfo?.data?.category_exp) > currentUnixTime ||
      Number(accountInfo?.data?.category2_exp) > currentUnixTime
    ) {
      setVip(true);
    } else {
      setVip(false);
    }
  });

  useEffect(() => {
    dispatch(fetchUserInfo());
  }, [dispatch]);

  return (
    <div className="flex flex-col items-center pb-16">
      <div className="w-full p-4">
        <Foreground />
        {!isVip && (
          <div
            className={`${getFontClass(`${language}_m`)} mt-4 p-4 bg-focusText text-white rounded flex items-center justify-between`}
            style={{
              background: "linear-gradient(45deg, #49265b, #212c49, #000000)",
            }}
          >
            <span className="text-base">
              {payment?.currency[language].symbol}
              <span className="text-lg font-bold"> {monthlyAmount} </span>
              {t("/月 全館暢看!!!")}
            </span>
            <VipButton />
          </div>
        )}
        <MyItems />
      </div>
      <Modal
        open={showPaymentStatusModal}
        content={[<div>{isPaymentSuccess ? t("付費成功") : t("付費失敗")}</div>]}
        onClose={() => {
          dispatch(setIsPaymentSuccess(false));
          dispatch(setShowPaymentStatusModal(false));
        }}
      />
    </div>
  );
};

export default Member;
