import React, { useState } from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemIcon from "@mui/material/ListItemIcon";
import IconButton from "@mui/material/IconButton";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleRight,
  faCrown,
  faList,
  faSackDollar,
  faDownload,
  faUser,
  faTicket,
  faQuestionCircle,
  faEnvelope,
  faCog,
} from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState, store } from "../../../store";
import Modal from "../../../components/Modal";
import { isLogin } from "../../../utils/helper";
import { useTranslation } from "react-i18next";
import { icon } from "@fortawesome/fontawesome-svg-core";

interface Item {
  title: string;
  icon: any; // FontAwesomeIcon doesn't have a specific type for icons
  link?: string;
}

const MyItems: React.FC = () => {
  const { t } = useTranslation();
  const items: Item[] = [
    { title: t("儲值訂閱"), icon: faCrown, link: "/app/Recharge" },
    { title: t("我的清單"), icon: faList, link: "/app/Member/MyList" },
    {
      title: t("我的消費紀錄"),
      icon: faSackDollar,
      link: "/app/Member/MyPayRecord",
    },
    // { title: "下載", icon: faDownload, link: "/app/Member/Downloads" },
    { title: t("我的會員資料"), icon: faUser, link: "/app/Member/MemberInfo" },
     { title: t("帳號驗證"), icon: faUser, link: "/app/Member/AccountVerify" },
    { title: t("回饋券兌換"), icon: "Coupon", link: "/app/Member/MyCoupon" },
    { title: t("常見問題"), icon: faQuestionCircle, link: "/app/Member/FAQ" },
    {
      title: t("客服信箱"),
      icon: faEnvelope,
      link: "/app/Member/CustomerService",
    },
    { title: t("設定"), icon: faCog, link: "/app/Member/Setting" },
  ];
  const navigate = useNavigate();
  const userInfo = useSelector(
    (state: RootState) => state.userInfo.userInfoData?.data
  );
  const [isLoginRemindModalOpen, setIsLoginRemindModalOpen] =
    useState<boolean>(false);
  console.log("userInfo: ", userInfo);
  const handleClick = (item: Item) => {
    if (
      (item.link && isLogin(store.getState())) ||
      (item.link &&
        (item.title === "儲值訂閱" || item.title === t("儲值訂閱"))) ||
      (item.link &&
        (item.title === "常見問題" || item.title === t("常見問題"))) ||
      (item.link && (item.title === "設定" || item.title === t("設定")))
    ) {
      navigate(item.link, { state: { barTitle: item.title } });
    } else {
      setIsLoginRemindModalOpen(true);
    }
  };

  return (
    <>
      <List sx={{ width: "100%" }}>
        {items.map((item) => (
          <ListItem
            key={item.title}
            onClick={() => handleClick(item)}
            disableGutters
            secondaryAction={
              <IconButton
                aria-label="angle-right"
                // onClick={() => handleClick(item)}
              >
                {item.title === "ji32k7"}
                <FontAwesomeIcon
                  icon={faAngleRight}
                  className="text-primary-active"
                />
              </IconButton>
            }
          >
            <ListItemIcon sx={{ minWidth: 30 }}>
              {item.icon === "Coupon" ? (
                <img src={require("../../../images/CouponWhite.png")}/>
              ) : (
                <FontAwesomeIcon
                  icon={item.icon}
                  className="text-primary-active"
                />
              )}
            </ListItemIcon>
            <ListItemText primary={item.title} />
          </ListItem>
        ))}
      </List>
      <Modal
        open={isLoginRemindModalOpen}
        content={[<div>{t("請先登入")}</div>]}
        onClose={() => setIsLoginRemindModalOpen(false)}
      />
    </>
  );
};

export default MyItems;
